@include font-face("FontAwesome", "../fonts/FontAwesome/fontawesome-webfont");

body,
address {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300;
  color: $black;
  font-size: 1.4rem;
  font-style: normal;
  line-height: 2.2rem;
  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }
}

a {
  font-weight: 700;
  color: $c-brand;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.heading-1,
.heading-2,
.heading-3,
.heading-4,
.heading-5 {
  font-family: "Unica One", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

.heading-1 {
  font-size: 3.4rem;
  line-height: 3.4rem;

  @media (min-width: 365px) {
    font-size: 3.8rem;
    line-height: 3.8rem;
  }

  @include media-breakpoint-up(md) {
    font-size: 6.4rem;
    line-height: 6.4rem;
  }
}

.heading-2 {
  font-size: 3.2rem;
  line-height: 3.8rem;
  @include media-breakpoint-up(md) {
    font-size: 4.8rem;
    line-height: 5.6rem;
  }
}

.heading-3 {
  font-size: 2.4rem;
  line-height: 2.8rem;
  @include media-breakpoint-up(md) {
    font-size: 3.2rem;
    line-height: 3.8rem;
  }
}

.heading-4 {
  font-size: 1.5rem;
  line-height: 2rem;
  letter-spacing: 2px;
  @include media-breakpoint-up(md) {
    font-size: 1.8rem;
    line-height: 2rem;
  }
}

.heading-5 {
  font-size: 1.5rem;
  line-height: 1.6rem;
  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 1.8rem;
  }
}

.paragraph,
p {
  font-size: 1.4rem;
  line-height: 2.2rem;
  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }
}

.btn-main {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.8rem;
  @include media-breakpoint-up(md) {
    font-size: 1.6rem;
    line-height: 1.9rem;
  }
}

.navigation__link {
  color: $c-black;
  font-size: 2.2rem;
  font-weight: 500;
  text-decoration: none;
  @include media-breakpoint-up(md) {
    font-size: 3rem;
  }
  @include media-breakpoint-up(xl) {
    font-size: 1.5rem;
    line-height: 1.8rem;
    text-transform: uppercase;
    font-weight: 700;
  }
}

.section-title-small {
  z-index: 5;
  color: $c-white;
  text-transform: uppercase;
  margin-bottom: 2rem;
  display: block;
  position: relative;
  display: inline-block;
  &::after {
    content: "";
    z-index: -1;
    left: -15%;
    position: absolute;
    top: -35%;
    width: 130%;
    height: 170%;
    background-color: $c-brand;
    transform-origin: center;
    display: block;
    transform: rotate(-5deg);
  }
  &--no-rotate {
    margin-bottom: 3rem;
    &::after {
      transform: rotate(0);
    }
  }
}
